				<div class="input-wrap">
					<input type="password" @keyup="test_regx($event.target)" @keydown="test_regx($event.target)" id="join-re-pw" @click="remove_null_input_border($event.target)" class="join-popup-input" placeholder=" " />
					<span class="warning-text blind"> .</span>
				</div>
				<div class="input-wrap">
