				<div class="input-wrap">
					<input type="number" @keyup="test_regx($event.target)" @keydown="test_regx($event.target)" id="join-tel" @click="remove_null_input_border($event.target)" class="join-popup-input" placeholder="(- )" autocomplete="off" />
