				</div>
				<div class="input-wrap">
					<input type="number" @keyup="test_regx($event.target)" @keydown="test_regx($event.target)" id="join-birth" @click="remove_null_input_border($event.target)" class="join-popup-input" placeholder="( )" autocomplete="off" />
					<span class="warning-text blind"> .</span>
				</div>
			</section>
			<section class="join-popup-btn-section">
				<input type="button" @click="do_join" id="join-btn" value="">
			</section>
		</div>
		<!-- END JOIN SECTION -->

		<!-- START FIND-ID SECTION -->
		<div id="find-id-section" class="find-section blind">
			<section class="find-popup-logo-section">
