			</section>
			<section class="confirm-btn-section">
				<div id="logout-btn" @click="do_logout" class="confirm-yesBtn"></div>
				<div id="logout-closeBtn" @click="close_logout_popup" class="confirm-noBtn"></div>
			</section>
		</div>

		<!-- !!! MY PAGE !!! -->
		<!-- START MODIFY-PW SECTION -->
		<div id="modify-pw-section" class="modify-section blind">
			<section class="modify-popup-title-section">
					<span> </span>
			</section>
			<section class="modify-popup-input-section">
					<div class="modify-popup-input-list">
							<div class="modify-popup-input-list-title">
									<span class="modify-guide-txt"> </span>
									<span class="modify-error-txt blind">  .</span>
							</div>
							<div class="modify-now-pw-check-section">
									<input type="password" @click="remove_null_input_border($event.target)" id="modify-pw-now" class="modify-popup-input-short" placeholder="  ."/>
									<input type="button" @click="do_check_now_pw" id="check-now-pw" class="modify-popup-check-btn" value=""/>
							</div>
					</div>

					<div class="modify-popup-input-list">
							<div class="modify-popup-input-list-title">
									<span class="modify-guide-txt">  </span>
									<span class="modify-error-txt blind">  .</span>
							</div>
							<input type="password" @click="remove_null_input_border($event.target)" @keyup="check_modify_pw_regx($event.target)" @keydown="check_modify_pw_regx($event.target)" id="modify-pw-new" class="modify-popup-input" placeholder="(++ = 8~10)"/>
					</div>

					<div>
							<div class="modify-popup-input-list-title">
									<span class="modify-guide-txt">  </span>
									<span class="modify-error-txt blind"> .</span>
							</div>
							<input type="password" @click="remove_null_input_border($event.target)" @keyup="check_modify_pw_regx($event.target)" @keydown="check_modify_pw_regx($event.target)" id="modify-pw-renew" class="modify-popup-input" placeholder="   ."/>
					</div>
			</section>
			<section class="modify-popup-btn-section">
					<div id="modify-btn" class="modify-btn"></div>
					<div id="modify-close-btn" class="modify-btn" @click="close_modify_pw_popup"></div>
			</section>
		</div>
		<!-- END MODIFY-PW SECTION -->

		<!-- START MODIFY PROFILE IMAGE -->
		<form action="/rence/user_img_updateOK" method="post" enctype="multipart/form-data">
			<div id="modify-img-section" class="confirm-popup blind">
				<section class="review-upload-section">
					<input type="text" class="review-upload-value" value="" readonly />
					<span class="review-upload-btn" @click="show_upload_file">   </span> 
					<input type="file" @change="select_user_img" class="file" name="multipartFile">
				</section>
				<section class="confirm-btn-section">
					<input type="submit" @submit="before_submit_modify_user_img" id="modify-img-modifyBtn" class="confirm-yesBtn" value="">
					<div id="modify-img-closeBtn" @click="close_modify_img_popup" class="confirm-noBtn" ></div>
				</section>
			</div>
		</form>
		<!-- END MODIFY PROFILE IMAGE -->
	
		<!-- START USER_DELETE CUSTOM CONFIRM POPUP -->
		<div id ="user-delete-confirm-popup" class="confirm-popup blind">
				<section class="confirm-txt-section">
						<span class="common-confirm-txt"> ?</span>
				</section>
				<section class="confirm-btn-section">
						<div id="user-delete" class="confirm-yesBtn" @click="do_user_delete"></div>
						<div id="user-delete-popup-close" class="confirm-noBtn" @click="close_user_delete_popup"></div>
				</section>
		</div>

		<!-- !!! QUESTION PAGE !!! -->
		<!-- START Q-DELETE CONFIRM POPUP -->
		<div id ="q-delete-popup" class="confirm-popup blind">
				<section class="confirm-txt-section">
						<span class="logout-txt">
								  ?
						</span>
				</section>
				<section class="confirm-btn-section">
						<div id="q-delete-btn" @click="do_q_delete" class="confirm-yesBtn"></div>
						<div id="q-delete-closeBtn" @click="close_q_delete_popup" class="confirm-noBtn"></div>
				</section>
		</div>
		
		<!-- START R-DELETE CONFIRM POPUP -->
		<div id="r-delete-popup" class="confirm-popup blind">
			<section class="confirm-txt-section">
				<span class="logout-txt">   ? </span>
			</section>
			<section class="confirm-btn-section">
				<div id="r-delete-btn" @click="do_r_delete" class="confirm-yesBtn"></div>
				<div id="r-delete-closeBtn" @click="close_r_delete_popup" class="confirm-noBtn"></div>
			</section>
		</div>

    <!--        -->
		<!-- START RESERVE-CANCLE CONFIRM POPUP -->
    <div id ="reserve-cancel-popup" class="confirm-popup blind">
      <section class="confirm-txt-section">
        <span class="reserve-cancle-txt">
            ?<br><br>
               .
        </span>
      </section>
      <section class="confirm-btn-section">
        <div id="refund-btn" @click="do_refund" class="confirm-yesBtn"></div>
        <div id="refund-closeBtn" @click="close_refund_popup" class="confirm-noBtn"></div>
      </section>
    </div>
    <!-- END RESERVE-CANCLE CONFIRM POPUP -->
    
    <!-- START RESERVE-CANCLE CONFIRM POPUP -->
    <div id ="reserve-cancel-confirm-popup" class="confirm-popup blind">
        <section class="confirm-txt-section">
            <span class="reserve-cancle-txt">
                  .
            </span>
        </section>
        <section class="confirm-btn-section">
