        </section>
    </div>
    <!-- END RESERVE-CANCLE-CONFIRM CONFIRM POPUP -->
    
    <!-- START RESERVE-CANCLE FAIL POPUP -->
    <div id ="reserve-cancel-fail-popup" class="confirm-popup blind">
