</template>

<style lang="scss" scoped >
 @import '@/assets/CSS/office/reserve_info.scss';
</style>

<script>
import axios from 'axios';
import $ from 'jquery';

export default {
  name: 'NowReserveInfoView',
  data() {
    return {
      list: '',
      reserveNo: '',
      axiosFlag: true,
    };
  },
  mounted() {
    //    ->  
    axios.get('http://localhost:8800/loginCheck')
      .then((response) => {
        //   
        if (response.data.result === '1') {
          this.$store.commit('office_setLogin_true');

          /** ********** *** */
          /** * GET DATA *** */
          /** ********** *** */

          //  
          $('.popup-background:eq(1)').removeClass('blind');
          $('#spinner-section').removeClass('blind');

          this.reserveNo = this.$route.params.parameters.split('reserve_no=')[1];

          axios.get(`http://localhost:8800/rence/reserve_info?reserve_no=${reserveNo}`)
            .then((res) => {
              this.list = res.data;
              this.load = true;

              //  
              $('.popup-background:eq(1)').addClass('blind');
              $('#spinner-section').addClass('blind');
            })
            .catch(() => {
              //  
              $('.popup-background:eq(1)').addClass('blind');
              $('#spinner-section').addClass('blind');

              $('.popup-background:eq(1)').removeClass('blind');
              $('#common-alert-popup').removeClass('blind');
              $('.common-alert-txt').text('    .');
            });
        }
        //    (or  )
        else {
          this.$store.commit('office_setLogin_false');
          $('.popup-background:eq(0)').removeClass('blind');
          $('#disconnect-session-popup').removeClass('blind');
        }
      })
      .catch(() => {
        $('.popup-background:eq(1)').removeClass('blind');
        $('#common-alert-popup').removeClass('blind');
        $('.common-alert-txt').text('      .');
      });
  },
  methods: {
    show_cancle_popup() {
      if (this.axiosFlag) {
        this.axiosFlag = false;

        //    ->  
        axios.get('http://localhost:8800/loginCheck')
          .then((response) => {
            this.axiosFlag = true;

            //   
            if (response.data.result === '1') {
              this.$store.commit('office_setLogin_true');

              $('.popup-background:eq(0)').removeClass('blind');
              $('#reserve-cancel-popup').removeClass('blind');
            }
            //    (or  )
            else {
              this.$store.commit('office_setLogin_false');
              $('.popup-background:eq(0)').removeClass('blind');
              $('#disconnect-session-popup').removeClass('blind');
            }
          })
          .catch(() => {
            this.axiosFlag = true;

            $('.popup-background:eq(1)').removeClass('blind');
            $('#common-alert-popup').removeClass('blind');
            $('.common-alert-txt').text('      .');
          });
      }
    },
  }, // END methods()
};
</script>
